<template>
 <div id="comment">
    <h3></h3>
    <hr>
    <textarea placeholder="120" maxlength="120"></textarea>
    <mt-button type="primary" size="large"></mt-button>
    <!--  -->
    <div class="cmt-list">
        <div class="cmt-item">
            <div class="cmt-title">
                1&nbsp;&nbsp;&nbsp;&nbsp;20194621:17:46
            </div>
            <div class="cmt-body">
                
            </div>
        </div>
        <div class="cmt-item">
            <div class="cmt-title">
                1&nbsp;&nbsp;&nbsp;&nbsp;20194621:17:46
            </div>
            <div class="cmt-body">
                
            </div>
        </div>
        <div class="cmt-item">
            <div class="cmt-title">
                1&nbsp;&nbsp;&nbsp;&nbsp;20194621:17:46
            </div>
            <div class="cmt-body">
                
            </div>
        </div>

    </div>
    <mt-button type="danger" plain size="large"></mt-button>

 </div>
</template>

<script>
import  {Toast} from 'mint-ui';
export default {
 data() {
 return {
     pageIndex:1, //
     list:[]      //
 }
 },
 methods: {
     getComment(){
         var url = 'api/getcomments/'+this.id+'?pageIndex=' + this.pageIndex;
         this.$http.post(url).then(response => {
             if(response.body.status === 0){
                 this.list = response.body.message;
             }else{
                 Toast("");
             }
         },err => {

         })
     }
 },mounted(){
     this.getComment();
 },props:['id']
}
</script>

<style lang="scss" scoped>
    #comment{
        h3{
            font-size: 18px;
        }
        textarea{
            font-size: 14px;
            margin:0;
            height: 85px;
        }

        .cmt-list{
            margin: 5px 0;
            .cmt-item{
                font-size: 13px;
                .cmt-title{
                    line-height: 30px;
                    background-color: #ccc;
                }
                .cmt-body{
                    line-height: 35px;
                    // 
                    text-indent: 2em;
                }
            }
        }
    }
</style>




